<style>
.v-card__title {
    font-weight: bold;
}

.ttrsnack .v-sheet.v-snack__wrapper {
    min-width: 200px;
}


</style>
<template>
<div>

       <v-col cols="12" md="12">
                        
                   
        <v-row dense>
            <v-col cols="12" md="12">
                <v-card>
                  <v-card-title style="font-size: 18px;">စနစ်အသုံးပြုသူများ<v-tooltip bottom>
    <template v-slot:activator="{ on: tooltip }">

         <v-btn
      class="mx-2"
      fab
      dark
      x-small
      color="primary"
      v-bind="attrs"
          v-on="{...tooltip}" @click.stop="showDatalistsEntry=true"
    >
      <v-icon dark>
        mdi-plus
      </v-icon>
      
    </v-btn>

    </template>
      <span>အသစ်ထည့်ရန်</span>
                 </v-tooltip>     
                 <UserEntry v-model="showDatalistsEntry" />  </v-card-title>
                  
           <v-col cols="12" class="mb-0 pt-0 pb-0">
           <v-row dense>

                                       <v-col cols="12" md="1"></v-col>
                                        <v-col cols="12" md="6"></v-col>
                                        <v-col cols="12" md="3">
                                            <v-text-field outlined dense clearable placeholder="ရှာဖွေရန်"></v-text-field>
                                        </v-col>
                                           <v-col cols="12" md="2">
                                            <v-btn color="primary" small dark>Search</v-btn>
                                        </v-col>
           </v-row>
        </v-col>
                    <v-col cols="12" md="12">

                     

                        <v-data-table :headers="headers" :items="ttrloglist">
                            
                            <template v-slot:item="row">
                                <tr>
                                    <td>
                                         <v-row>
                                            <v-col>

                                                
                                        <v-btn icon color="orange">
                                                <v-icon>mdi-pencil</v-icon>
                                            </v-btn>
                                           
                                            <v-btn icon color="red">
                                                <v-icon>mdi-trash-can</v-icon>
                                            </v-btn>
                                              <router-link 
                        :to="{
                          path: '/password_reset',
                          query: { id: row.item.id },
                        }"
                        tag="v-btn"
                        outlined
                      >
                        <v-tooltip bottom color="green">
                          <template v-slot:activator="{ on }">
                            <v-icon class="mr-2" color="green" dark v-on="on"
                              >mdi-lock-reset</v-icon
                            >
                          </template>
                          <span>Password Reset</span>
                        </v-tooltip>
                      </router-link>
                                            </v-col>
                                         </v-row>
                                    </td>
                                     <td> {{row.item.indx}}
                                     </td>
                                         <td>{{row.item.name}}</td>

                                            <td>{{ row.item.address}}</td>

                                 <td>{{ row.item.state}}</td>
                                   
                                
                                    <td>{{row.item.district}}</td>
                                      <td>{{row.item.township}}</td>
                                        <td style="">{{row.item.remark}}</td>
                                    <td style="">
                                        
                                        <v-chip small  style="margin-top:1px" color="info">{{ row.item.status }}</v-chip>
                                        </td>
                                
                                   
                                </tr>
                            </template>
                        </v-data-table>
                        
                    </v-col>
                </v-card>
            </v-col>
        </v-row>
    </v-col>
</div>
</template>

<script>
/*eslint-disable*/
import TTRLogSearch from '../models/ttrlogsearch'
import UserEntry from '../views/UserEntry'

export default {
    components: {
        UserEntry,
    },

    data() {
        return {
            showDatalistsEntry: false,
            snackbar: false,
            text: "",
            timeout: "",
            color: "",
            x: "right",
            y: "top",
            id: 0,
            headers: [{
                    sortable: false,
                    text: "Actions",
                    width: 150
                },
              
                {
                    sortable: false,
                    text: "စဉ်",
                    value: "indx",
                    width: 70
                },
                {
                      sortable: false,
                    text: "အမည်",
                    value: "name",
                    width: 150
                },

                 {
                       sortable: false,
                    text: "အဆင့်",
                    value: "address",
                    width: 150
                },

                {
                    sortable: false,
                    text: "ရာထူး/ တာဝန်",
                    value: "state",
                    width: 120
                },
                {
                    text: "ဌာန",
                    value: "district",
                    width: 120
                },
                {
                    text: "မြို့နယ်",
                    value: "township",
                    width: 120
                },
                {
                    sortable: false,
                    text: "ဖုန်းနံပတ်",
                    value: "remark",
                    width: 100
                },
                {
                    sortable: false,
                    text: "Status",
                    value: "status",
                    width: 120
                },
               
                

            ],
            searchmodel: new TTRLogSearch(),
            ttrloglist: [],
            branchcodelist: [],
            loading: false,
            totallog: 0,
            pagination: {
                descending: true,
                page: 1,
                itemsPerPage: 50,
                pageStart: 1,
                pageStop: null,
                sortBy: ["id"]
            },
            footerProps: {
                "items-per-page-options": [50, 100, 200, 500, 1000]
            },
        }

    },
   
    watch: {
        params: {
            handler() {
                this.GetAllData();
            },
            deep: true
        },
    },
    created() {
        this.$emit("eventname", true);
         this.GetAllData();
    },
    methods: {
        
        GetAllData() {
            var vm = this;
            vm.ttrloglist =  [
                
                {id: 1, indx: '၁', name: 'ဦးအောင်သူရ', address: 'Reporting Officer', state: 'ဦးစီးအရာရှိ',  district: "စီမံ", township: "Zabuthiri",  status: 'Active', remark: '၀၉-၉၀၉၁၀၁၉၉၃'},
                {id: 2, indx: '၂', name: 'ဦးရွှေဝင်း', address: 'Reporting Officer', state: 'ဦးစီးအရာရှိ',  district: "စီမံ", township: "Zabuthiri", status: 'Active', remark: '၀၉-၉၀၉၁၀၁၉၉၃'},
                {id: 3, indx: '၃',  name: 'ဦးဇော်နိုင်ဝင်း',address: 'Reporting Officer', state: 'ဦးစီးအရာရှိ', district: "စီမံ", township: "Zabuthiri",  status: 'Active', remark: '၀၉-၉၀၉၁၀၁၉၉၃'},
                {id: 4, indx: '၄',  name: 'ဒေါ်မိုးဝေ', address: 'Administrator', state: 'ဦးစီးအရာရှိ', district: "စီမံ", township: "Zabuthiri", status: 'Active', remark: '၀၉-၉၀၉၁၀၁၉၉၃'},
            ];
        },

   

    }
};
</script>
