        <v-text-field style="margin-top:10px;"
           outlined dense
            required
          ></v-text-field>
        </v-col>

      
             <v-col
         cols="12"
          md="4"
          sm="4"
        > 

        <label class="lab">အဆင့်</label>     <v-combobox style="margin-top:10px;"
          :items="['Administrator', 'Reporting Officer']"
          outlined
          dense
        ></v-combobox></v-col>

             <v-col
         cols="12"
          md="4"
          sm="4"
        > <label class="lab">ရာထူး/ တာဝန်</label><v-text-field style="margin-top:10px;"
           outlined dense
            required
          ></v-text-field></v-col>

      </v-row>

        <v-row>
               <v-col
         cols="12"
          md="3"
          sm="3"
        > 
        

    
        <label class="lab">ဌာန</label>     <v-combobox style="margin-top:10px;"
          :items="['စီမံ', 'egov', 'ဘဏ္ဍာရေး']"
          outlined
          dense
        ></v-combobox></v-col>
        <v-col
         cols="12"
          md="3"
          sm="3"
        > 
        

    
        <label class="lab">မြို့နယ်</label>     <v-combobox style="margin-top:10px;"
          :items="['Zabuthiri', 'Zayyarthiri']"
