          dense
        ></v-combobox></v-col>

                     <v-col
         cols="12"
          md="3"
          sm="3"
        > 
        

    
        <label class="lab">ဖုန်းနံပတ်</label>     
        <v-text-field style="margin-top:10px;"
           outlined dense
            required
          ></v-text-field></v-col>

      <v-col
         cols="12"
          md="3"
          sm="3"
