        <v-combobox style="margin-top:10px;"
          :items="['Active', 'Inactive']"
          outlined
          dense
        ></v-combobox>

     </v-col>
        </v-row>

       <v-row>
           <v-col
         cols="12"
          md="4"
          sm="4"
        > 
        

    
        <label class="lab">အသုံးပြုသူအမည်</label>     
        <v-text-field style="margin-top:10px;"
           outlined dense
            required
          ></v-text-field></v-col>

                     <v-col
         cols="12"
          md="4"
          sm="4"
        > 
        

    
        <label class="lab">Password</label>     
