<script>


export default {


  data() {
    return { 
      
    };},


      props: {
     value: Boolean
  },
  computed: {
    show: {
      get () {
        return this.value
      },
      set (value) {
         this.$emit('input', value)
      }
    }
  }

  
  

 

}
</script>