<template>
  <div id="app">
    <v-app id="inspire">
      <v-app id="inspire">
        <NavigationDrawer v-if="valid" />
        <v-content>
          <router-view @eventname="updateparent"></router-view>
        </v-content>
      
      </v-app>
    </v-app>
  </div>

 
</template>

<script>
import NavigationDrawer from "./views/NavigationDrawer";
export default {
  name: "App",
  components: {
    NavigationDrawer,
  },
  data() {
    return {
      drawer: null, // Hide mobile side menu by default
      valid: false,
    };
  },
  methods: {
    updateparent(val) {
      this.valid = val;
    },
  },
};
</script>
