import axios from 'axios';
import authHeader from '../services/auth-header';

const API_URL = process.env.VUE_APP_API_ENDPOINT;

class UserService {

  login(user) {
    // return axios.get(API_URL + "/user/authenticate");
    return axios.post(API_URL + "/user/authenticate",
      {
        userName: user.name,
        password: user.password,
      },
      {
        headers: authHeader(),
      }
    )
      .then((response) => {
        if (response.data.token) {
          localStorage.setItem("user", JSON.stringify(response.data));
        }
        return response.data;
      });
  }

  InsertData(user) {
   
    return axios.post(API_URL + "/user/saveorupdate",
      {
        Id: user.id,
        UserName: user.username,
        Password: user.password,
        Role: user.role,

      }, { headers: authHeader() });
  }

  InsertPassword(user) {
    
    return axios.post(API_URL + "/user/resetpassword",
      {
        Id: user.id,
        UserName: user.username,
        Password: user.password,
        Role: user.role,

      }, { headers: authHeader() });
  }

   GetById(id)
  {
      return axios.get(API_URL+'/user/GetByid?id='+id,{headers:authHeader()});
  }

  DeleteData(user)
  {
   return axios.delete(API_URL+'/user/'+user.id,{headers:authHeader()});
  }

  GetAll() {
    let config = {
      headers: authHeader(),
      params: {
        draw: 1,
        sortBy: "id",
        sortOrder: "desc",
        start: 0,
        length: -1,

      }
    }
    return axios.get(API_URL + "/user/GetAllData", config);

  }
}

export default new UserService();