<style>
.v-card__title {
    font-weight: bold;
}
</style>
<template>
<div>
    <v-col cols="12" md="12">
        <v-row dense>
            <v-col cols="12" md="12">
                <v-card>
                    <v-card-title class="ctitle pt-2 pb-2">
                        <router-link :to="{ name: 'User', params: { success: true } }" tag="v-btn" outlined>
                            <v-icon color="primary" class="mr-3">mdi-arrow-left-box</v-icon>
                        </router-link>

                        <h4>User Entry</h4>
                        <v-snackbar :color="color" v-model="snackbar" :right="x" :timeout="timeout" :top="y">
                            {{ text }}
                            <template v-slot:action="{ attrs }">
                                <v-btn color="white" v-bind="attrs" text @click="snackbar = false">
                                    <v-icon>mdi-close-circle</v-icon>
                                </v-btn>
                            </template>
                        </v-snackbar>
                    </v-card-title>
                    <v-divider></v-divider>

                    <v-col cols="12" md="12">
                        <v-card-text>
                            <v-container>
                                <v-form autocomplete="off" @submit.prevent="Insert()" ref="form" v-model="valid" lazy-validation>
                                    <v-row dense>
                                        <v-col cols="12" md="12">
                                            <v-row dense class="mt-2 mb-n2">
                                                <v-col cols="12" md="3" class="text-right">
                                                    <label class="lab">အမည်<span class="red--text title">*</span></label>
                                                </v-col>
                                                <v-col cols="12" md="8">
                                                    <v-text-field v-model="user.username" required outlined dense></v-text-field>
                                                    <div class="red--text" v-if="requireusername">
                                                        {{ getUserName_Message }}
                                                    </div>
                                                </v-col>
                                                <v-col cols="12" md="3" class="text-right">
                                                    <label class="lab">Password<span class="red--text">*</span></label>
                                                </v-col>
                                                <v-col cols="12" md="8">
                                                    <v-text-field v-model="user.password" required :rules="passwordRules" :append-icon="
                                showpassword ? 'mdi-eye' : 'mdi-eye-off'
                              " :type="showpassword ? 'text' : 'password'" @click:append="showpassword = !showpassword" outlined single-line dense></v-text-field>
                                                    <div class="red--text" v-if="requirepassword">
                                                        {{ getPassword_Message }}
                                                    </div>
                                                    <div class="red--text" v-if="!passwordlenght">
                                                        {{ passwordlengthmsg }}
                                                    </div>
                                                </v-col>
                                                <v-col cols="12" md="3" class="text-right">
                                                    <label class="lab">Role<span class="red--text title">*</span></label>
                                                </v-col>
                                                <v-col cols="12" md="8">
                                                    <v-autocomplete v-model="user.role" :items="items" off required outlined dense></v-autocomplete>
                                                    <div class="red--text" v-if="requirerole">
                                                        {{ getRole_Message }}
                                                    </div>
                                                </v-col>
                                            </v-row>

                                            <v-row dense class="mb-n2">
                                                <v-col cols="12" md="3"> </v-col>
                                                <v-col cols="12" md="8" class="text-right">
                                                    <v-btn text type="submit" small outlined color="#fff" class="mr-3" style="background-color: rgb(37 140 244)">save</v-btn>
                                                    <v-btn class="ml-1" @click="Clear" small color="warning">Reset</v-btn>
                                                </v-col>
                                            </v-row>
                                        </v-col>
                                    </v-row>
                                </v-form>
                            </v-container>
                        </v-card-text>
                    </v-col>
                </v-card>
            </v-col>
        </v-row>
    </v-col>
</div>
</template>

<script>
import User from "../models/user";
import UserService from "../services/user.service";
import Constant from "../constant";

export default {
    data() {
        return {
            user: new User(),
            showpd: false,
            showpassword: false,
            headers: [{
                    text: "Index",
                    value: "case_no",
                    width: 150
                },
                {
                    text: "Username",
                    value: "case_reference_no",
                    width: 200
                },
                {
                    text: "Role",
                    value: "case_type",
                    width: 200
                },
                {
                    text: "Actions",
                    value: "action",
                    sortable: false,
                    width: 230
                },
            ],
            items: [{
                    text: "ADMIN",
                    value: "ADMIN"
                },
                {
                    text: "USER",
                    value: "USER"
                },
            ],

            snackbar: false,
            text: "",
            timeout: "",
            color: "",
            x: "right",
            y: "top",

            requireusername: false,
            requirepassword: false,
            requirerole: false,
            passwordlenght: false,
            // passworddisable: true,
        };
    },
    watch: {
        "user.role"() {
            if (this.user.role != "" && this.user.role != null) {
                this.requirerole = false;
            } else {
                this.requirerole = true;
            }
        },
        "user.username"() {
            if (this.user.username != "" && this.user.username != null) {
                this.requireusername = false;
            } else {
                this.requireusername = true;
            }
        },
        "user.password"() {
            if (this.user.password != "" && this.user.password != null) {
                this.requirepassword = false;
                this.checkpasswordlength();
            } else {
                this.requirepassword = true;
                this.passwordlenght = true;
            }
        },
    },
    computed: {
        getRole_Message() {
            return Constant.EmptyMessageForRole;
        },
        getUserName_Message() {
            return Constant.EmptyMessageForUserName;
        },
        getPassword_Message() {
            return Constant.EmptyMessageForPassword;
        },
        passwordlengthmsg() {
            return Constant.PasswordLengthMessage;
        },
    },
    created() {
        this.$emit("eventname", true);
        this.user.id = this.$route.query.id;

        if (this.user.id > 0) {
            this.GetDataById();
            // this.passworddisable = true;
        }
    },
    methods: {
        checkpasswordlength() {
            if (this.user.password.length < 6) {
                this.passwordlenght = false;
            } else {
                this.passwordlenght = true;
            }
        },

        CheckRequire() {
            if (this.user.role != "" && this.user.role != null) {
                this.requirerole = false;
            } else {
                this.requirerole = true;
            }
            if (this.user.username != "" && this.user.username != null) {
                this.requireusername = false;
            } else {
                this.requireusername = true;
            }
            if (this.user.password != "" && this.user.password != null) {
                this.requirepassword = false;
                this.checkpasswordlength();
            } else {
                this.requirepassword = true;
                this.passwordlenght = true;
            }
        },

        Insert() {
            var vm = this;
            vm.CheckRequire();
            if (
                vm.user.username != "" &&
                vm.user.password != "" &&
                vm.user.role != "" && vm.passwordlenght == true
            ) {
                UserService.InsertPassword(vm.user).then((response) => {
                    var message = response.data.messages[0];
                    if (response.data.success) {
                        vm.color = "success";
                        vm.text = message;
                        vm.snackbar = true;
                    } else {
                        vm.color = "red darken-2";
                        vm.text = message;
                        vm.snackbar = true;
                    }
                });
            }
        },

        GetDataById() {
            var vm = this;

            UserService.GetById(vm.user.id).then((response) => {
                vm.user.id = response.data.id;
                vm.user.username = response.data.userName;
                vm.user.password = response.data.password;
                vm.user.role = response.data.role;
                this.user.password = "";
            });
        },

        Clear() {
            this.user.username = "";
            this.user.password = "";
            this.user.role = "";
        },
    },
};
</script>
