<style>
.v-card__title {
    font-weight: bold;
}

.ttrsnack .v-sheet.v-snack__wrapper {
    min-width: 200px;
}


</style>
<template>
<div>

       <v-col cols="12" md="12">
                        
                   
        <v-row dense>
            <v-col cols="12" md="12">
                <v-card>
                  <v-card-title style="font-size: 18px;">Report Lists</v-card-title>
                  
            <v-col cols="12" md="12">
        <v-btn size="small" class="ml-3" href="/PEPform" color="#2196f3" style="color: #fff; font-weight: bold;">ဩဇာတိက္ကမရှိသူလူပုဂ္ဂိုလ်စာရင်းရေးသွင်းခြင်းပုံစံ</v-btn>
      </v-col> 
 <v-col cols="12" md="12">
    <v-expansion-panels>
                            <v-expansion-panel>
                                <v-expansion-panel-header>ရှာဖွေရန် </v-expansion-panel-header>
                                <v-expansion-panel-content>
                                    <v-row dense>

                                    
                                        <v-col cols="12" md="3">
                                             <v-text-field v-model="searchmodel.TransactionNumber" outlined dense clearable placeholder="Serial No"></v-text-field>
                                        </v-col>
                                        <v-col cols="12" md="3">
                                            <v-autocomplete v-model="searchmodel.BranchCode" :items="branchcodelist" item-text="code" item-value="code" outlined dense clearable placeholder="From Date"></v-autocomplete>
                                        </v-col>
                                         <v-col cols="12" md="3">
                                            <v-autocomplete v-model="searchmodel.BranchCode" :items="branchcodelist" item-text="code" item-value="code" outlined dense clearable placeholder="To Date"></v-autocomplete>
                                        </v-col>
                                        <v-col cols="12" md="3">
                                           <v-autocomplete v-model="searchmodel.BranchCode" :items="branchcodelist" item-text="code" item-value="code" outlined dense clearable placeholder="Username"></v-autocomplete>
                                        </v-col>
                                        <v-col cols="12" md="3">
                                            <v-autocomplete v-model="searchmodel.BranchCode" :items="branchcodelist" item-text="code" item-value="code" outlined dense clearable placeholder="Status"></v-autocomplete>
                                        </v-col>
                                        <v-col cols="12" md="3" class="">
                                            <v-btn @click="Filter" small text outlined color="#fff" class="mr-2" style="background-color: rgb(37 140 244)">Search</v-btn>
                                            <v-btn class="ml-2" small @click="ResetFilter" text outlined color="#fff" style="background-color: rgb(255 82 0)">Reset</v-btn>
                                            <v-btn color="success" class="ml-3" small dark @click="ExcelReport" style="background-color: #4caf50; color: #fff;">
                                                Export Excel
                                                <span slot="loader" class="custom-loader">
                                                    <v-icon light>mdi-cached</v-icon>
                                                </span>
                                            </v-btn>
                                        </v-col>
                                        <!-- <v-col cols="12" md="2">
                                            <v-btn color="success" dark @click="ExcelReport" style="background-color: #4caf50; color: #fff;">
                                                Export Excel
                                                <span slot="loader" class="custom-loader">
                                                    <v-icon light>mdi-cached</v-icon>
                                                </span>
                                            </v-btn>
                                            <a href="#" id="mylink"></a>
                                        </v-col> -->
                                    </v-row>
                                </v-expansion-panel-content>
                            </v-expansion-panel>
                        </v-expansion-panels>
</v-col>
                    <v-col cols="12" md="12">

                     

                        <v-data-table :headers="headers" :items="ttrloglist">
                            
                            <template v-slot:item="row">
                                <tr>
                                    <td>
                                         <v-row>
                                            <v-col>

                                                 <router-link to="#" tag="v-btn">
                                            <v-btn icon color="green">
                                                <v-icon>mdi-eye</v-icon>
                                            </v-btn>
                                            </router-link>
                                        <v-btn icon color="orange">
                                                <v-icon>mdi-pencil</v-icon>
                                            </v-btn>
                                           
                                            <v-btn icon color="red">
                                                <v-icon>mdi-trash-can</v-icon>
                                            </v-btn>
                                            </v-col>
                                         </v-row>
                                    </td>
                                     <td> {{row.item.indx}}
                                     </td>
                                         <td>{{row.item.name}}</td>

                                            <td>{{ row.item.address}}</td>

                                 <td><v-chip small  style="margin-top:1px; color: #fff;" color="#ff7300">{{ row.item.state}}</v-chip></td>
                                   
                                
                                    <td>{{row.item.district}}</td>
                                    <td style="">{{row.item.township}}
                                        
                                    </td>
                                    <td style="">
                                        
                                        {{ row.item.status }}
                                        </td>
                                 
                                   
                                </tr>
                            </template>
                        </v-data-table>
                        
                    </v-col>
                </v-card>
            </v-col>
        </v-row>
    </v-col>
</div>
</template>

<script>
/*eslint-disable*/
import TTRLogSearch from '../models/ttrlogsearch'

export default {
    components: {
        
    },

    data() {
        return {
            showSchoolEntry: false,
            snackbar: false,

            text: "",
            timeout: "",
            color: "",
            x: "right",
            y: "top",
            id: 0,
             items: [
        { title: 'Click Me' },
        { title: 'Click Me' },
        { title: 'Click Me' },
        { title: 'Click Me 2' },
      ],
            headers: [{
                    sortable: false,
                    text: "Actions",
                    width: 150
                },
              
                {
                    sortable: false,
                    text: "စဉ်",
                    value: "indx",
                    width: 50
                },
                {
                      sortable: false,
                    text: "အမည်",
                    value: "name",
                    width: 200
                },

                 {
                       sortable: false,
                    text: "အခြားအမည်",
                    value: "address",
                    width: 110
                },

                {
                    sortable: false,
                    text: "မွေးသက္ကရာဇ်",
                    value: "state",
                    width: 120
                },
                {
                    text: "ပညာအရည်အချင်း",
                    value: "district",
                    width: 120
                },
                {
                    text: "အလုပ်အကိုင်",
                    value: "township",
                    width: 100
                },
                {
                    sortable: false,
                    text: "နေရပ်လိပ်စာ",
                    value: "status",
                    width: 200
                },
            
                

            ],
            searchmodel: new TTRLogSearch(),
            ttrloglist: [],
            branchcodelist: [],
            loading: false,
            totallog: 0,
            pagination: {
                descending: true,
                page: 1,
                itemsPerPage: 50,
                pageStart: 1,
                pageStop: null,
                sortBy: ["id"]
            },
            footerProps: {
                "items-per-page-options": [50, 100, 200, 500, 1000]
            },
        }

    },
   
    watch: {
        params: {
            handler() {
                this.GetAllData();
            },
            deep: true
        },
    },
    created() {
        this.$emit("eventname", true);
         this.GetAllData();
    },
    methods: {
        
        GetAllData() {
            var vm = this;
            vm.ttrloglist =  [
                
                {id: 1, indx: '1', name: 'ဦးအောင်', address: 'အောင်ကြီး', state: '29-11-1969',  district: "B.A (English)", township: "ကုန်သည်", status: "အမှတ်-၁၇၊ လသာမြို့နယ်၊ ရန်ကုန်တိုင်းဒေသကြီး"},
               
            ];
        },

   

    }
};
</script>
